.user-single {
    width: 960px;
    margin: 0 auto;
    @media screen and (max-width: 960px) {
        width: 90%;
        margin: 0 auto; } }
.headband {
    display: block;
    .banner {
        height: 30vh;
        position: relative;
        img {
            position: absolute;
            height: 30vh;
            width: 100%;
            overflow: hidden;
            border-radius: 10px;
            z-index: 1; } }
    .no-image.banner {
        object-fit: cover !important; }
    .profile {
        margin: 0vh 0em 1em 2em;
        position: relative;
        display: grid;
        grid-template-columns: 172px auto;
        z-index: 1;
        .photo img {
            width: 172px;
            height: 172px;
            border-radius: 300px;
            border: 7px solid #fff;
            margin-top: -10vh;
            margin-bottom: 0; }
        .navbio {
            margin-left: 1.5rem;
            display: flex;
            align-items: center;
            .meta {
                margin-top: .75em;
                h1 {
                    font-size: 1.2rem;
                    font-weight: 500; }
                p {
                    font-size: .98rem;
                    font-weight: 300;
                    padding-top: 0; } }
            .menu {
                display: flex;
                list-style: none;
                margin-left: auto;
                li {
                    margin-left: 2em; }
                li a {
                    border: var(--border-line);
                    color: var(--text);
                    padding: .9em 2em;
                    font-size: .8em;
                    border-radius: 7px;
                    font-weight: 500; } } } } }
.maindiv {
    display: grid;
    grid-template-columns: auto 30%;
    grid-gap: 3rem;
    .section {
        margin-bottom: 3em;
        .consub {
            ul {
                padding-inline-start: 0;
                align-items: center; }
            li {
                font-size: .975rem;
                list-style: none;
                border-left: 1px solid orange;
                padding-left: 1em;
                align-item: center;
                text-transform: capitalize; } } }
    .headsub {
        font-size: 1.2rem;
        margin-bottom: .5em; }
    .section {
        p {
            font-size: .9rem; } }
    asside {
        .section {
            .consub {
                li {
                    border-left: var(--border-line);
                    padding-left: 0;
                    margin-top: 2em; } }
            .sub-title {
                display: flex;
                align-items: center;
                border-bottom: var(--border-line);
                flex-direction: row-reverse;
                img {
                    margin: 0; }
                span {
                    font-size: .955em;
                    font-weight: 500;
                    margin-right: auto;
                    padding-left: 1em; } }
            .meta {
                padding: 1em 0 1em 1em;
                display: flex;
                align-items: center;
                .value {
                    font-size: .875em;
                    text-transform: lowercase; }
                .action {
                    font-size: .8em;
                    margin-left: auto; } } } } }



