
html[data-mode="dark"] .team-list {
    .card {
        background: var(--accent); } }
.team-list {
    padding-left: 0 !important;
    .card {
        display: grid;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        background: var(--bg);
        border: var(--border-line);
        padding: 1em;
        text-align: center;
        @media screen and (max-width: 672px) {
            display: block; }
        .linked {
            @media screen and (max-width: 672px) {
                width: 100%;
                display: inline-flex; } }
        figure {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 1em;
            @media screen and (max-width: 672px) {
                margin-right: 2em;
                margin-bottom: 0em; }
            img,
            .no-image {
                width: 72px !important;
                height: 72px !important;
                border-radius: 150px;
                margin: auto 0; } }
        .prime {
            @media screen and (max-width: 672px) {
                display: inline-flex;
                align-items: center;
                margin-bottom: .25em; } }
        .name {
            font-size: .9em;
            @media screen and (max-width: 672px) {
                text-align: left;
                margin-right: .7rem;
                padding-right: .7em;
                padding-top: 0;
                padding-bottom: 0;
                border-right: var(--border-line); } }
        .position {
            text-transform: uppercase;
            font-size: .7em; }
        .action {
            display: flex;
            border: var(--border-line);
            font-size: .75em;
            border-radius: 7px;
            overflow: hidden;
            margin-top: 1em;
            background: var(--bg);
            @media screen and (max-width: 672px) {
                margin-top: .75em; }
            .contact,
            .detail {
                padding: 7px 25px;
                color: var(--text); }
            .contact {}
            .detail {
                border-right: var(--border-line); } }
        .contact-box {
            display: none; }
        .contact-box.modal {
            display: grid;
            align-items: center;
            justify-items: center;
            position: fixed;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, .5);
            z-index: 1000000; }
        .modal {
            .context {
                border: var(--border-line);
                border-radius: 10px;
                z-index: 100000001;
                overflow: hidden;
                display: block;
                position: absolute;
                width: 90vw;
                max-height: 90vh; }
            .head {
                background: var(--bg);
                padding: 1em;
                border-bottom: var(--border-line);
                font-size: .85rem;
                .name {
                    font-size: .875rem;
                    font-weight: 600;
                    border-right: 0; } }
            .body {
                padding: 2em;
                background: var(--accent);
                overflow-y: scroll;
                height: 75vh;
                .title {
                    font-size: 1em;
                    border-bottom: var(--border-line);
                    text-align: left;
                    padding-left: 1em;
                    font-size: .85em; }
                sub-title {}

                .box {
                    border-left: var(--border-line); }
                .connect {
                    font-size: .8em;
                    p {
                        font-weight: 600;
                        text-transform: capitalize; }
                    a {
                        font-size: .975em; }
                    .item {
                        text-align: left;
                        list-style: none; }
                    .meta {
                        display: flex;
                        align-items: center; }
                    .copy {
                        margin-left: auto; } } }
            .foot {
                padding: 0.85em 1em;
                border-top: var(--border-line);
                font-size: .85em;
                background: var(--bg); } } } }




